import React, { useState, useEffect } from "react";
import "./Projet.css";

export default function Slides() {
  const [showScrollTop, setShowScrollTop] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 400) {
        setShowScrollTop(true);
      } else {
        setShowScrollTop(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  const slides = [
    {
      id: 1,
      title: "Locatio",
      text: "Notre mission tait de crer un site permettant  professionnels et particuliers de proposer des produits  la location, pour quelques heures ou plusieurs mois. Jai pris en charge la partie administration ainsi que le dveloppement des pages listant les produits disponibles...",
      image: "/locatio.png",
      link: "",
    },
    {
      id: 2,
      title: "Chat GPT x Majoli",
      text: "Durant mon alternance, jai travaill  amliorer le site de lentreprise en automatisant la cration des descriptions...",
      image: "majolichat.png",
      link: "",
    },
    {
      id: 3,
      title: "La chtite Marseillaise",
      text: "Jai particip  la cration du site de 'La Chtite Marseillaise', une artiste locale renomme...",
      image: "lachtite.png",
      link: "https://lachtitemarseillaise.com/",
    },
    {
      id: 4,
      title: "Pat VTC",
      text: "Jai travaill sur 'Pat VTC', un site web ddi  un service de VTC permettant aux utilisateurs de slectionner une adresse...",
      image: "/patvtc.png",
      link: "https://patvtc.fr/",
    },
    {
      id: 5,
      title: "CogSpace",
      text: "Lors dun projet scolaire, jai particip  la cration dun site web de base de connaissances...",
      image: "/cogspace.png",
      link: "",
    },
    {
      id: 6,
      title: "EcoSphere",
      text: "Lors dune semaine intensive  lInstitut G4, nous avons travaill sur une plateforme de sensibilisation cologique...",
      image: "ecoservice.png",
      link: "",
    },
    {
      id: 7,
      title: "We Are Jolies",
      text: "Lors dun projet scolaire, jai particip  la cration dun site web de base de connaissances...",
      image: "/waj2.png",
      link: "https://wearejolies.com/",
    },
    {
      id: 8,
      title: "Circle",
      text: "Pour ce projet, nous avons cr un site e-commerce pour une marque de produits coresponsables...",
      image: "/circle.png",
      link: "",
    },
    {
      id: 9,
      title: "Hangman",
      text: " mon entre  lcole Epitech, jai intgr une phase intensive appele la piscine...",
      image: "/hangman.png",
      link: "https://github.com/nnevvw/hangman",
    },
  ];

  return (
    <div className="relative">
      {slides.map((slide, index) => (
        <div
          key={slide.id}
          className={`slide slide${slide.id} ${
            index % 2 === 0 ? "row" : "row-reverse"
          }`}
        >
          <a href={slide.link} target="_blank" rel="noopener noreferrer">
            <img src={slide.image} alt={slide.title} className="slide-image" />
          </a>
          <div className="slide-content">
            <h2>{slide.title}</h2>
            <p>{slide.text}</p>
          </div>
        </div>
      ))}

      {/*  Bouton scroll to top */}
      {showScrollTop && (
        <button className="scroll-to-top" onClick={scrollToTop}>
          
        </button>
      )}
    </div>
  );
}
